.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 11;
  /* background-color: magenta; */
}

.container .background {
  aspect-ratio: 3/2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  border: none;
  border-radius: 1vw;
  padding: 0.75vw 5vw;
  /* border: 0.25vw solid var(--main-menu); */
  background-color: white;
  @media (min-width: 641px) {
    width: 50vw;
  }

  @media (max-width: 640px) {
    width: 90vw;
  }
}

.container .background .formPass {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  /* row-gap: 0.25vw; */
  /* background-color: greenyellow; */
}

.container .background .formPass h2 {
  margin-bottom: 1vw;
  width: 100%;
  text-align: center;
  /* background-color: aqua; */
}

.container .background .formPass label {
  font-size: 1.25;
  font-weight: 500;
}

.container .background .formPass input {
  width: 100%;
  border: none;
  border-radius: 1vw;
  margin-bottom: 0.75vw;
  padding: 0.25vw 1vw;
  background-color: aliceblue;
}

.container .background .formPass .warn {
  display: flex;
  flex-direction: column;
  border-radius: 0.5vw;
  font-size: 0.85vw;
  background-color: var(--header-color);
  padding: 0.25vw 1vw;
}

.container .background .formPass .warn p {
  color: white;
}

.container .background .formPass .warn p span {
  color: red;
}

.container .background .formPass .warnOff {
  display: none;
}

.container .background .formPass .buttons {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  column-gap: 1.5vw;
  margin: 1vw 0;
  /* background-color: aqua; */
}
